<template>
  <div class="login">
    <notification-messages v-if="errorMess !== 'null'" :typeOfMess="errorMess"/>
 
    <form-login>
      <template v-slot:header>
        <h1 class="form__title">Вход в систему</h1>
      </template>
    </form-login>
  </div>
</template>

<script>
import notificationsAlerts from '@/components/notificationAlerts';
import formLogin from '@/components/formLogin';

export default {
  components:{
    'notification-messages' : notificationsAlerts,
    'form-login' : formLogin,
  },
  data: function(){
    return{
      errorMess: 'null',
    }
  },
  mounted() {
    //errorMess writing to localstorage by form-login component if the error happens during the auth.
    localStorage.errorMess ? this.errorMess = localStorage.errorMess : null;
    localStorage.errorMess = null;
  },
}
</script>

