<template>
  <div class="allert__mes">
    <b-message :type="typeOfNotification" has-icon auto-close>  
      {{ allertMessage }}
    </b-message>
  </div>
</template>

<script>
export default {
  data: function (){
    return{
      typeOfNotification: 'is-warning',
    }
  },
  props:{
    typeOfMess: {
      type: String,
      required: true,
    }
  },
  computed:{
    allertMessage(){
      if(this.typeOfMess){
        switch(this.typeOfMess){
          case 'passwordError' : 
            this.typeOfNotification = 'is-danger';
            return 'Неправильный логин или пароль. Пожалуйста, попробуйте еще раз.';

          case 'manyLogin' : 
            this.typeOfNotification = 'is-warning';
            return 'Cлишком много попыток. Попробуйте, пожалуйста, позже.'

          case 'networkError' : 
            this.typeOfNotification = 'is-warning';
            return 'Похоже, у вас проблемы с интрнетом. Проверьте ваше соеденение.'
        }
      }
    }
  }
}
</script>