<template>
  <div class="form__container">
    <div class="preloader">
      <b-notification ref="element" :closable="false" class="preloader"></b-notification>
    </div> 

    <slot name="header"></slot>

    <form id="loginform" @submit.prevent="checkValidation" class="form__login" novalidate="true">
      <div class="form__alert-validation" v-if="errors.length > 0">
        <p>Корректно заполните поле(я):</p>
        <ul>
          <li class="form__alert-list" v-for="(error,index) in errors" :key="index">{{ error }}</li>
        </ul>
      </div>

      <b-field>
        <b-input id="login" expanded required
          type="email"
          v-model="login"
          placeholder="Введите ваш логин"
          validation-message="Некоректно введена почта"
          custom-class="form__login-field"
        ></b-input>
      </b-field>

      <b-field>
        <b-input id="password" password-reveal expanded  required
          type="password"
          v-model="password"
          placeholder="Введите ваш пароль"
          validation-message="Поле пароль не может быть пустым"
          custom-class="form__login-field"     
        ></b-input>
      </b-field>

      <input class="form__login-field form__login-submit" type="submit" value="Войти"/>
    </form>
  </div>
</template>

<script>
import * as firebase from "firebase/app";
import "firebase/auth";

export default {
  data: function(){
    return{
      password: null,
      login: null,
      errors: [],
    }
  },
  methods: {
    authorization(){ 
      const loader = this.$buefy.loading.open({container: this.$refs.element.$el}); //preloader start
      firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION)
        .then( _=>{    
          firebase.auth().signInWithEmailAndPassword(this.login, this.password)
            .then( res => {
              const currentUser = firebase.auth().currentUser.providerData[0].email;
              this.$store.dispatch('setLoginUser', currentUser);
            })
            .then( _=>{
              const link = this.$route.query.redirect || '/dashboard';
              loader.close(); //preloader finish
              this.$router.push(link); 
            })
            .catch(error => {
              console.log('Error:'+error.message);

              switch(error.message){
                case 'There is no user record corresponding to this identifier. The user may have been deleted.' :
                  this.$router.go();
                  localStorage.errorMess = 'passwordError';
                  break; 
                case 'The password is invalid or the user does not have a password.' :
                  this.$router.go();
                  localStorage.errorMess = 'passwordError';
                  break;
                case 'Too many unsuccessful login attempts. Please try again later.' :
                  this.$router.go();
                  localStorage.errorMess = 'manyLogin'; 
                  break;  
                case 'A network error (such as timeout, interrupted connection or unreachable host) has occurred.' :
                  this.$router.go();
                  localStorage.errorMess = 'networkError';  
                  break;      
              }
            }); 
        })     
    },
    checkValidation: function(e){
      if(this.login && this.password && this.validEmail(this.login)){ this.authorization() }
      else{
        this.errors = [];

        if(!this.login) {this.errors.push("Поле логина")}
        else if(!this.validEmail(this.login)){this.errors.push("Поле логина")}

        !this.password ? this.errors.push("Поле пароля") : null;
      }
    },
    validEmail: function (email) {
      let reg = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return reg.test(email);
    }
  },
  created(){
    if (!firebase.apps.length) {
      const firebaseConfig = this.$store.getters.getfirebaseConfig;
      firebase.initializeApp(firebaseConfig);
      this.$store.dispatch('setLoginUser', null);
    }
  },
}
</script>